@import url(https://fonts.googleapis.com/css2?family=Cedarville+Cursive&display=swap);
* {
  margin: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  margin: 0;
  background-color: rgb(241, 241, 241);
  height: auto;
  width: auto;
}

.login {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: linear-gradient(-120deg, #003087, #009cde, #012169);
}

.login__container {
  display: flex;
  flex-direction: column;
  padding: 20px;
  width: 700px;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
}

.login__logo {
  display: flex;
  justify-content: center;
  height: 100px;
}

.login__desc {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 10px;
}

.login__desc > h3 {
  margin-top: 10px;
  text-align: center;
  font-size: xx-large;
  font-family: "Cedarville Cursive", cursive;
}
.login__desc > p {
  color: gray;
  text-align: center;
  font-weight: 500;
  font-size: 20px;
}

.login__auth {
  display: flex;
  margin-top: 50px;
}

.login__authOptions {
  display: flex;
  flex: 0.5 1;
  flex-direction: column;
  padding: 20px;
  border-right: 1px solid lightgray;
}

.login__authOption {
  display: flex;
  align-items: center;
  padding: 7px;
  margin-bottom: 15px;
  border: 1px solid lightgray;
  border-radius: 5px;
  cursor: pointer;
}

.login__authOption:hover {
  background-color: rgb(226, 226, 226);
}

.login__authOption > img {
  height: 30px;
  object-fit: contain;
}

.login__authOption > p {
  margin-left: 11px;
}

.login__authDesc {
  display: flex;
  align-items: center;
  padding: 7px;
  margin-bottom: 15px;
}
.login__authDesc > p {
  font-size: small;
  color: gray;
}

.login__emailPass {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px;
  flex: 0.5 1;
  margin-left: 20px;
}

.login__label > h4 {
  padding: 5px;
}

.login__inputFields {
  display: flex;
  flex-direction: column;
  padding: 5px;
}

.login__inputField {
  padding: 5px;
}

.login__inputField > input {
  padding: 10px;
  width: 100%;
  outline: none;
  border: none;
  background-color: rgb(236, 236, 236);
}

.login__forgButt {
  padding-left: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.login__forgButt > small {
  color: gray;
  cursor: pointer;
}

.login__forgButt > small:hover {
  color: blue;
  text-decoration: underline;
}

.login__forgButt > button {
  padding: 9px;
  border-radius: 5px;
  border: none;
  outline: none;
  background-color: blue;
  color: white;
  font-weight: bold;
  font-size: 15px;
  cursor: pointer;
}

.login__emailPass > button {
  padding: 9px;
  border-radius: 5px;
  border: none;
  outline: none;
  background-color: blue;
  color: white;
  font-weight: bold;
  font-size: 15px;
  cursor: pointer;
  margin-top: 10px;
}

.login__lang {
  display: flex;
  align-items: center;
  justify-content: center;
  border-top: 1px solid lightgray;
  border-bottom: 1px solid lightgray;
  margin-top: 30px;
  padding: 10px;
}

.login__lang > p {
  color: gray;
  margin-right: 5px;
  cursor: pointer;
  font-size: 13px;
}

.login__lang > p:hover {
  color: gray;
  margin-right: 5px;
  text-decoration: underline;
}

.login__lang > .MuiSvgIcon-root {
  color: gray;
}

.login__footer {
  padding-top: 10px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.login__footer > p {
  color: gray;
  font-size: small;
  cursor: pointer;
}

.login__footer > p:hover {
  text-decoration: underline;
}

.quoraBox {
  display: flex;
  flex-direction: column;
  padding: 10px;
  border: 1px solid lightgray;
  background-color: white;
  border-radius: 5px;
  cursor: pointer;
}

.quoraBox:hover {
  border: 1px solid lightskyblue;;
}

.quoraBox__info {
  display: flex;
  align-items: center;
}

.quoraBox__info > h5 {
  color: rgb(129, 129, 129);
  font-weight: 300;
  margin-left: 10px;
}

.quoraBox__quora {
  display: flex;
  margin-top: 8px;
}

.quoraBox__quora > p {
  color: gray;
  font-weight: bold;
  font-size: large;
}
.feed {
  display: flex;
  flex-direction: column;
  flex: 0.4 1;
}

.post {
  display: flex;
  flex-direction: column;
  padding: 10px;
  background-color: white;
  margin-top: 10px;
  border: 1px solid lightgray;
  border-radius: 5px;
  box-shadow: 0px 5px 8px -9px solid rgab(0, 0, 0, 0.5);
}

.post__info {
  display: flex;
  align-items: center;
}

.post__info > h4 {
  margin-left: 10px;
  cursor: pointer;
  font-size: 13px;
}
.post__info > h4:hover {
  text-decoration: underline;
}

.post__info > small {
  margin-left: 10px;
}

.post__body {
  display: flex;
  flex-direction: column;
}

.post__body > .post__question {
  margin-top: 10px;
  font-weight: bold;
  margin-bottom: 10px;
  cursor: pointer;
  display: flex;
  align-items: center;
  flex: 1 1;
}

.post__question > .post__btnAnswer {
  margin-left: auto;
  cursor: pointer;
  padding: 7px;
  background-color: blue;
  outline: none;
  border: none;
  color: lightgray;
  font-weight: 300;
  font-size: 14px;
  border-radius: 33px;
}

.post__btnAnswer:hover {
  color: blue;
  background: lightgray;
}

.post__question > p:hover {
  text-decoration: underline;
}

.post__body > .post__answer > p {
  margin-bottom: 10px;
}

.post__body > img {
  width: 100%;
  object-fit: contain;
  background-color: rgb(238, 238, 238);
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.post__footer {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.post__footer > .MuiSvgIcon-root {
  color: grey;
  margin-right: 10px;
  cursor: pointer;
  margin-left: 10px;
}

.post__footerAction {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 35px;
  padding: 5px;
  background-color: lightgrey;
}
.post__footerAction > .MuiSvgIcon-root {
  color: gray;
  margin-right: 40px;
  cursor: pointer;
}

.post__footerAction > .MuiSvgIcon-root:hover {
  color: #4F206E;
  margin-right: 40px;
}

.post__footerLeft {
  margin-left: auto;
}

.post__footerLeft > .MuiSvgIcon-root {
  color: gray;
  cursor: pointer;
  margin-left: 30px;
}

.modal__question {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.modal__question > h1 {
  color: #4F206E;
  font-weight: 600;
  margin-bottom: 10px;
}

.modal__question > p {
  color: gray;
  font-size: small;
}

.modal__question > p > .name {
  color: black;
  font-weight: bold;
}

.modal__answer {
  display: flex;
  padding-top: 20px;
  flex: 1 1;
}

.modal__answer > textarea {
  width: 100%;
  height: 200px;
  padding: 5px;
  font-size: 15px;
  color: blue;
}

.modal__button {
  display: flex;
  align-items: center;
  margin-right: -20px;
  border-top: 1px solid lightgray;
}

.modal__button > .cancle {
  margin-left: auto;
  border: none;
  margin-top: 5px;
  outline: none;
  color: gray;
  font-weight: 500;
  padding: 10px;
  margin-right: 10px;
  border-radius: 33px;
  cursor: pointer;
}
.modal__button > .add {
  border: none;
  outline: none;
  margin-top: 5px;
  background-color: rgb(0, 64, 255);
  color: white;
  font-weight: 700;
  padding: 10px;
  margin-right: 10px;
  border-radius: 33px;
  cursor: pointer;
}
.qHeader {
  display: flex;
    justify-content: center;
    align-items: center;
    padding: 2px;
    background-color: white;
    width: 100%;
    position: -webkit-sticky;
    position: sticky;
    z-index: 1000;
    top: 0;
    box-shadow: 0px 5px 8px -9px rgba(0 , 0, 0, 0.5);
    max-height: 50px;
    cursor: pointer;
}

.qHeader__logo > img {
    height: 35px;
    width: 70px;
    margin-right: 20px;
}

.qHeader__icons {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.qHeader__icons > .active > .MuiSvgIcon-root {
  fill: #4F206E;;
}

.qHeader__icon > .MuiSvgIcon-root {
  font-size: xx-large;
  background-color: white;
  padding: 5px 20px;
  color: gray;
}

.qHeader__icon > .MuiSvgIcon-root:hover {
  background-color: lightskyblue;
  border-radius: 10px;
  color: #4F206E;
}

.qHeader__input {
  display: flex;
  align-items: center;
  border: 1px solid lightskyblue;
  padding: 5px;
  border-radius: 5px;
  margin-left: 5px;
}

.qHeader__input > input {
  background-color: transparent;
  outline: none;
  border: none;
  color: #4F206E;
}

.qHeader__input > .MuiSvgIcon-root {
  color: gray;
}

.qHeader__avatar {
  cursor: pointer;
}
.qHeader__Rem {
  display: flex;
  align-items: center;
  margin-left: 25px;
}

.qHeader__Rem > .MuiSvgIcon-root {
  font-size: xx-large;
  color: gray;
  margin-left: 25px;
  cursor: pointer;
}
.qHeader__Rem > .MuiSvgIcon-root:hover {
  color: #4F206E;
}

.qHeader__Rem > .MuiButton-root {
  color: white;
  background: #4F206E;
  text-transform: inherit;
  border-radius: 20px;
  margin-left: 25px;
}

.qHeader__Rem > .MuiButton-root:hover {
  color: #ffffff;
  background: #9b57c9;;
}

.modal__title {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
  border-bottom: 1px solid rgba(187, 187, 187, 0.5);
}

.modal__title > h5 {
  color: gray;
  font-size: 20px;
  cursor: pointer;
  font-weight: 500;
  margin-right: 30px;
}

.modal__title > h5:hover {
  color: #4F206E;
}

.modal__info {
  display: flex;
  align-items: center;
  margin-top: 30px;
}

.modal__info > p {
  margin-left: 10px;
  font-size: small;
  color: gray;
}

.modal__info > .modal__scope {
  display: flex;
  align-items: center;
  color: rgb(98, 98, 98);
  padding: 5px;
  margin-left: 10px;
  background-color: rgb(230, 230, 230);
  border-radius: 33px;
  cursor: pointer;
}

.modal__Field {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  flex: 1 1;
}

.modal__Field > .modal__fieldLink {
  color: gray;
  display: flex;
  margin-top: 10px;
  align-items: center;
}

.modal__Field > .modal__fieldLink > input {
  flex: 1 1;
  border: none;
  outline: none;
  margin-left: 5px;
}

.modal__buttons {
  display: flex;
  align-items: center;
  margin-top: 260px;
  margin-right: -20px;
  border-top: 1px solid lightgray;
}

.modal__buttons > .cancle {
  margin-left: auto;
  border: none;
  margin-top: 5px;
  outline: none;
  color: gray;
  font-weight: 500;
  padding: 10px;
  margin-right: 10px;
  border-radius: 33px;
  cursor: pointer;
}
.modal__buttons > .add {
  border: none;
  outline: none;
  margin-top: 5px;
  background-color: rgb(0, 64, 255);
  color: white;
  font-weight: 700;
  padding: 10px;
  margin-right: 10px;
  border-radius: 33px;
  cursor: pointer;
}

.quora__content {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}

.sidebarOptions {
  display: flex;
  flex-direction: column;
  position: -webkit-sticky;
  position: sticky;
  top: 100px;
}
.sidebarOption {
  display: flex;
  align-items: center;
  padding: 10px;
  cursor: pointer;
}

.sidebarOption:hover {
  background:lightskyblue;
  border-radius: 5px;
  margin-right: 20px;
}
.sidebarOption > img {
  height: 30px;
  border-radius: 5px;
  margin-left: 20px;
}

.SideBar_Option1{
  display: flex;
  align-items: center;
  padding: 10px;
  cursor: pointer;
  /* border: 1px solid grey; */
  border-radius: 5px;
  background-color: #E6E7E8;
  margin-right: 20px;
}

.SideBar_Option1:hover{
    background-color: lightskyblue;
    border-radius: 8px;
    transition: 0.5s;
}

.SideBar_Option1 > p{
    color: grey;
    font-weight: 400;
    margin-left: 5px;
}


.SideBar_Option1 > .MuiSvgIcon-root{
    color: grey;
    font-weight: medium;
    margin-left: 20px;
    border: 1px solid lightgrey;
    border-radius: 6px;
    background-color: #E6E7E8;
}

.sidebarOption > .MuiSvgIcon-root {
  font-size: medium;
  margin-left: 20px;
}
.sidebarOption > p {
  margin-left: 10px;
  color: gray;
  font-weight: 400;
  font-size: 13px;
}

.sidebarOption > .text {
  margin-left: 10px;
  color: gray;
  font-weight: 400;
  font-size: 13px;
}

.sidebar {
  margin-right: 10px;
  margin-left: 10px;
  flex: 0.15 1;
}

.widget {
  flex: 0.15 1;
  margin-left: 10px;
  display: flex;
  flex-direction: column;
  padding: 10px;
  margin-right: 30px;
  border: 1px solid lightgray;
  border-radius: 5px;
  max-height: 85vh;
  background-color: white;
}

.widget__header {
  border-bottom: 1px solid lightgray;
}

.widget__header > h5 {
  padding: 10px;
  color: rgb(80, 80, 80);
  font-size: 15px;
  letter-spacing: 1.1px;
}

.widget__contents {
  display: flex;
  flex-direction: column;
}
.widget__contents {
  display: flex;
  cursor: pointer;
}

.widget__content {
  margin: 5px;
  display: flex;
  padding: 5px;
  border-bottom: 1px solid lightgray;
}

.widget__content > img {
  height: 30px;
  border-radius: 5px;
}

.widget__contentTitle {
  margin-left: 10px;
}

.widget__contentTitle > h5 {
  color: rgb(70, 68, 68);
}

.widget__contentTitle > p {
  color: gray;
  font-size: small;
}

